.app {
    font-size: 1.4rem;
    @media screen and (max-width: 960px) {
        font-size: 1.4rem;
    }
    .aside {
        // height: 100vh;
        overflow-y: auto;
        .logo {
            height: 3rem;
            margin: 1rem;
            box-sizing: border-box;
            border-radius: 1rem;
            padding: 0 1rem;
            text-align: center;
            img {
                height: 100%;
            }
        }
    }

    .header {
        background-color: #fff;
        padding: 0 1.5rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        .right {
            display: flex;
            align-items: center;
            position: absolute;
            right: 20px;
        }
        .left {
            display: flex;
            align-items: center;
            position: absolute;
            left: 20px;
        }
    }

    .content {
        padding: 10px 10px;
        width: auto !important;
        height: 80%;
        // overflow: scroll;
    }

    .footer {
        text-align: center;
    }
}

@primary-color: #1DA57A;